.cloud-vertical-align-text-container{
    height: 100%;
    position: relative;
    width: 50px;
}

.cloud-vertical-align-text{
    position: absolute;
    left: 0.5em;
    top: 0.5em;
}

.portfolio-me-img-container{
    position: relative;
    height: 123px;
    width: 70px;
    margin-bottom: 10px;
}

.portfolio-me-img{
    height: 250px;
    -webkit-clip-path: inset(5px 20px 125px 40px);
    clip-path: inset(5px 20px 125px 40px);
    position: absolute;
    left: -54px;
    top: -4.5px;
}

.portfolio-me-img-parent{
    border-style: solid;
    border-radius: 10%;
    overflow: hidden;
    border-width: 1px;
}


.footer-portfolio{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
}

.cloud-button-centered-container{
    width: 100%;
    height: 40px;
    position: relative;
}

.cloud-button-centered{
    position: absolute;
    left: 0.5em;
}

.project-machines-buttons-container{
    width: 100%;
    position: relative;
    height: 100px;
}
  
.project-machines-buttons{
    position: absolute;
    left: 50%;
}

.project-name-container{
    width: 100%;
    position: relative;
    height: 100px;
}
  
.project-name{
    position: absolute;
    top: 25%;
}

.tech-modal{
    z-index: 10;
    position: absolute;
    left: 50vw;
    top: 50vh;
    transform: translate(-50%, -50%);
    width: 400px;
}

.fade-in{
    animation-name: fade-in;
    animation-duration: 1s;
 }
 
 .fade-out{
     animation-name: fade-out;
    animation-duration: 1s;
 }
 
 @keyframes fade-in{
   from{
     opacity: 0;
   }
   to{
     opacity: 1;
   }
 }
 
 @keyframes fade-out{
   from{
     opacity: 1;
   }
   to{
     opacity: 0;
   }
 }